body {
  background-color: aliceblue;
}

.hide {
  display: none;
}

header {
  text-align: center;
  border: 1px solid black;
  border-radius: 5px;
  margin: 15px;
  padding: 5px;
  background-color: rgba(51, 102, 51, 0.9);
  box-shadow: 1px 1px 4px 1px rgba(51, 102, 51, 0.9);
  color: aliceblue;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-shadow: 1px 1px 1px black;
}

header a {
  font-size: 20px;
  text-decoration: none;
  color: navy;
}

h1, h2 {
  margin: 5px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

#search-form {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

form {
  background-color: rgba(119, 85, 119, 0.9);
  padding: 10px;
  border-radius: 5px;
  border: 1px solid black;
  box-shadow: 1px 1px 4px 1px rgba(119, 85, 119, 0.9);
}

form input {
  padding: 10px;
  margin: 15px;
  border: 1px solid black;
  border-radius: 5px;
  font-size: 16px;
  box-shadow: 1px 1px 4px 1px rgba(50, 50, 50, 0.9); 
  background-color: aliceblue;
}

#search-results {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  padding: 10px;
  text-align: center;
  margin: 15px; 

}

.card {
  border: 1px solid black;
  border-radius: 5px;
  margin: 10px;
  background-color: rgba(35, 101, 131, 0.6);
  box-shadow: 1px 1px 6px 1px rgba(35, 101, 131, 0.6);
}

.card .card-header {
  padding: 20px 30px 0px;
}

.card-header a {
  text-decoration: none;
  color: navy;
}
.card-header a:hover {
  text-decoration: underline;
}

.card .card-body {
  padding: 20px 50px;
}

.card-body.row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.card-body.row p {
  width: auto; 
  font-family: Georgia, 'Times New Roman', Times, serif; 
  text-align: left;
  padding: 0px 50px;
  margin: 0px 50px 30px 30px;
  line-height: 1.5rem;
}

.card-body img {
  width: 130px;
  height: 200px;
  border-radius: 5px;
  border: 1px solid black;
  box-shadow: 1px 1px 4px 1px rgba(50,50,50,0.9);
  margin: 0px 20px 30px;
}

#buttons {
  text-align: right;
}
.card-body.row #buttons {
  padding-right: 50px;
  padding-bottom: 50px;
}

#buttons span {
  cursor: pointer;
}

footer {
  text-align: center;
  padding: 20px;
}